.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0); }

html {
  font-family: "PTMono-Regular", "Courier New", monospace;
  font-size: 16px;
  color: #666666; }

body {
  min-width: 320px;
  background-color: #E5E5E5; }

h1, h2, h3, h4, h5, p {
  margin: 0; }

a {
  text-decoration: none; }

@font-face {
  font-family: 'Roboto';
  font-weight: 500;
  src: url("../fonts/Robotomedium.woff2") format("woff2"), url("../fonts/Robotomedium.woff") format("woff"); }

@font-face {
  font-family: 'Robotocondensed';
  src: url("../fonts/Robotocondensed.woff2") format("woff2"), url("../fonts/Robotocondensed.woff") format("woff"); }

@font-face {
  font-family: 'Robotocondensedbold';
  src: url("../fonts/Robotocondensedbold.woff2") format("woff2"), url("../fonts/Robotocondensedbold.woff") format("woff"); }

@font-face {
  font-family: 'PTMono-Regular';
  src: url("../fonts/PTMono-Regular.woff2") format("woff2"), url("../fonts/PTMono-Regular.woff") format("woff"); }

