.page-header {
  display: flex;
  max-width: 1411px;
  margin: 0 auto;
  padding-top: 33px;
  padding-left: 25px;
  padding-right: 25px; }

.page-header-logo {
  margin-right: auto; }

.page-header__contacts {
  display: flex;
  align-items: center;
  list-style: none;
  margin: 0;
  padding: 0; }

.page-header__contacts-item {
  margin-left: 38px; }

.page-header__contacts-item-phone {
  margin-left: 88px; }

.page-header__contacts-item a {
  color: #FFFFFF; }
  .page-header__contacts-item a:hover {
    opacity: 0.8; }
  .page-header__contacts-item a:active {
    opacity: 0.7; }

.page-header__contacts-item-mail a {
  position: relative;
  padding-left: 39px; }
  .page-header__contacts-item-mail a::before {
    content: '';
    position: absolute;
    width: 19px;
    height: 14px;
    left: 0;
    top: 2px;
    background: url("../img/icon-mail.svg") no-repeat; }

.page-header__contacts-item-phone a {
  position: relative;
  padding-left: 34px; }
  .page-header__contacts-item-phone a::before {
    content: '';
    position: absolute;
    width: 18px;
    height: 18px;
    left: 0;
    background: url("../img/icon-phone-call.svg") no-repeat; }

.page-header__contacts-item-button a {
  display: block;
  min-width: 180px;
  padding: 14px;
  text-align: center;
  font-size: 14px;
  font-family: "Robotocondensed", "Arial", sans-serif;
  text-transform: uppercase;
  border: 1px solid #FFFFFF;
  border-radius: 5px;
  letter-spacing: 0.7px; }

@media (max-width: 1023px) {
  .page-header {
    padding-left: 25px;
    padding-right: 25px; }
  .page-header__contacts-item {
    margin-left: 25px;
    font-size: 0; }
  .page-header__contacts-item-button {
    display: none; }
  .page-header__contacts-item-mail a::before {
    content: '';
    width: 26px;
    height: 19px;
    top: -12px; }
  .page-header__contacts-item-phone a::before {
    content: '';
    width: 24px;
    height: 24px;
    top: -15px; } }

@media (max-width: 767px) {
  .page-header {
    padding-left: 15px;
    padding-right: 15px; } }

.page__logo-link {
  display: flex;
  align-items: center; }
  .page__logo-link:hover {
    opacity: 0.8; }
  .page__logo-link:active {
    opacity: 0.7; }

.page__logo-link img {
  margin-right: 16px; }

.page__logo-name {
  font-family: "Robotocondensedbold", "Arial", sans-serif;
  font-size: 25px;
  color: #FFFFFF; }

.page__logo-slogan {
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 12px;
  color: #788891; }

@media (max-width: 767px) {
  .page-header-logo .page-logo__container {
    display: none; }
  .page-header-logo img {
    width: 38px;
    height: 38px; } }
